<template>
  <div style="display: flex;justify-content: space-between">
    <div class="components-container">
      <el-row>
        <el-col :span="6">
          <el-button style="color:blue" @click="getcjd()">更新于：{{refreshTime}} 点此刷新</el-button>
        </el-col>
      </el-row>
      <br />

      <div style="width:1200px;height:600px">
        <el-row :gutter="20" justify="space-between">
          <el-col :span="4" v-for="(item,index) in cjdInfo" :key="index" class="box">
            <el-card>
              <img src="../img/online.jpg" v-if="item[index,2]==1" style="width:100%" alt />
              <img src="../img/offline.jpg" v-if="item[index,2]==0" style="width:100%" alt />
              <div class="info">
                <span>{{item[index,0]}}</span>
              </div>
              <div class="info">
                <span>{{item[index,1]}}</span>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
const defaultIsProductLine = ["jh"];
import moment, { now } from "moment";
export default {
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isCountByProductionLine: false,
      isRouterAlive: true,
      refreshTime: moment(new now()).format("yyyy-MM-dd hh:MM:ss"),
      note: {
        backgroundImage: "url(" + require("./../assets/logo.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "25px auto",
        marginTop: "5px"
      },
      beiruike: [],
      cjdInfo: []
    };
  },

  methods: {
    getcjd() {
      this.loading = true;
      this.refreshTime = this.dateFormatTime(new Date());
      var mycjdInfo = new Array();
      this.$http.get("/Cjd/ListCjd").then(resp => {
        if (resp) {
          var isonline = 1;
          this.beiruike = resp.data;
          let now = new Date();
          this.cjdInfo;
          for (var i = 0; i < resp.data.length; i++) {
            let zhgx = new Date(resp.data[i].zhgx);
            if (now - zhgx > 1200000) {
              isonline = 0;
            } else {
              isonline = 1;
            }
            mycjdInfo.push([
              resp.data[i].jh,
              this.dateFormatTime(zhgx),
              isonline
            ]);
          }
          this.cjdInfo = mycjdInfo;
        } else {
          this.loading = false;
          this.showMsg = true;
        }
      });
    },

    dateFormatTime: function(value) {
      if (value) {
        var formatterTime = moment(new Date(value)).format(
          "YYYY-MM-DD HH:mm:ss"
        );
        return formatterTime;
      } else {
        return "";
      }
    }
  },

  mounted() {
    this.getcjd();
    setInterval(this.getcjd, 600000); //定时任务，十分钟刷新一次；
  }
};
</script>

<style lang="scss" >
  .box {
    height: 80%;
    /*overflow-y: auto;*/
    display: flex;
    flex-direction: column; //起作用的是这个，没有的话会变成下图的样子，卡片跑到右边去了
  }
  .info {
    padding: 0px, 18px;
  }
  .index-box {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    .el-container {
      height: 100%;
      .el-main {
        height: 100%;
      }
    }
    .cell-istimeout {
      background: rgb(177, 122, 122);
      color: rgb(213, 233, 33);
    }
    .cell-isnormal {
      background: rgb(171, 187, 216);
      color: rgb(129, 67, 67);
    }
    .el-aside {
      background-color: #20222a;
      height: 100vh;
      min-height: 100%;
      .logo {
        background-color: #20222a;
        color: rgba(255, 255, 255, 0.8);
        text-align: center;
        height: 60px;
        padding-top: 16px;
        font-size: 16px;
        box-sizing: border-box;
        border-bottom: 1px solid gray;
      }
    }
    .el-header {
      border-bottom: 1px solid #f6f6f6;
      box-sizing: border-box;
      background-color: #fff;
      line-height: 60px;
    }
    .el-card {
      margin-bottom: 20px; //下边距
    }
  }
</style>
