import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login'
//import HelloWorld from './components/HelloWorld'
import NotFound from './views/NotFound'
import Index from './views/Index'
import Offline from './views/Offline'
import Cjdinfo from './views/Cjdinfo'
import FlCountByComp from './views/FlCountByComp'
import FlCountByProductLine from './views/FlCountByProductLine'
import Home from './views/Home'
import About from './views/About'
import Welcome from './views/Welcome'
import EchartDemo from './views/EchartDemo'

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path:'/login',
            component: Login,
            meta: {keepAlive:true}            
        },
        {
            path: '/home',
            component: Home,
            meta: {keepAlive:true},
            name:'首页',
            children: [

                {
                    path: '/404',
                    component: NotFound,
                    meta: {keepAlive:true}
                },
                {
                    path: '/welcome',
                    component: Welcome,
                    name: '欢迎',
                    meta: {keepAlive:true}
                },
                {
                    path:'/flcountbycomp',
                    component: FlCountByComp,
                    name: '方量统计（按公司）',
                    meta: {keepAlive:true}
                },
                {
                    path:'/flCountByProductionLine',
                    component: FlCountByProductLine,
                    name: '方量统计（按生产线）',
                    meta: {keepAlive:true}
                },
                {
                    path:'/echartDemo',
                    component: EchartDemo,
                    name: '明细汇总',
                    meta: {keepAlive:true}
                },
                {
                    path:'/offline',
                    component: Offline,
                    name: '离线统计',
                    meta: {keepAlive:true}
                },
                {
                    path:'/cjdinfo',
                    component: Cjdinfo,
                    name: '采集点信息',
                    meta: {keepAlive:true}
                },
                {
                    path: '/about',
                    component: About,
                    name: '关于',
                    meta: {keepAlive:true}
                }
            ]
        },
      
        {
            path: '*',
            redirect: {path: '/404'}
        }
    ]
})

router.beforeEach((to, from, next) => {
    const LOGINURL = '/login'
    let url, user
  
    url = to.path
    user = sessionStorage.getItem('user')
    to.meta.keepAlive = true
  
    if (url === LOGINURL) {
      sessionStorage.removeItem('user')
    }
  
    // 判断是否登录
    if (!user && url !== LOGINURL) {
      next({ path: LOGINURL })
    } else {
      next()
    }
  })
  
  export default router

