<template>
  <!--为echarts准备一个具备大小的容器dom-->
  <div>
    <el-form label-width="80px">
      <el-row>
        <el-col :span="6">
          <el-form-item label="起始日期">
            <el-date-picker
              v-model="startEndDate.qrq"
              type="date"
              format="yyyy-MM-dd 00:00:00"
              value-format="yyyy-MM-dd 00:00:00"
              placeholder="选择日期"
              :picker-options="pickerOption1"
            >起始日期</el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="结束日期">
            <el-date-picker
              v-model="startEndDate.zrq"
              type="date"
              format="yyyy-MM-dd 23:59:59"
              value-format="yyyy-MM-dd 23:59:59"
              placeholder="选择日期"
            >结束日期</el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-button @click="getOnlineStatus">查询</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div ref="main" style="width: 1200px;height: 150px;"></div>
  </div>
</template>
<script>
import echarts from "echarts";
import moment from "moment";
export default {
  name: "",
  data() {
    return {
      pickerOption1: {
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "上期",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "本期",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          }
        ]
      },
      startEndDate: {
        qrq: new Date(),
        zrq: new Date()
      },
      onlineStatus: [],
      isOnline: [],
      isOffline: [],
      echartData: [],
      mydata: []
    };
  },
  methods: {

    getOnlineStatus() {
      this.startEndDate.qrq = moment(this.startEndDate.qrq).format(
        "YYYY-MM-DD 00:00:00"
      );
      this.startEndDate.zrq = moment(this.startEndDate.zrq).format(
        "YYYY-MM-DD 23:59:59"
      );
      this.loading = true;
      this.$http
        .post("/OnlineStatus/ListOnlineStatus", this.startEndDate)
        .then(resp => {
          if (resp) {
            this.onlineStatus = resp.data;
            for (var i = 0; i < resp.data.length; i++) {
              this.echartData.push([
                new Date(resp.data[i].cjtime).getTime(),
                resp.data[i].status ? 300 : 200
              ]);
              if (resp.data[i].status) {
                this.isOnline.push([
                  new Date(resp.data[i].cjtime).getTime(),
                  300
                ]);
              } else {
                this.isOffline.push([
                  new Date(resp.data[i].cjtime).getTime(),
                  300
                ]);
              }
            }
          } else {
            this.loading = false;
            this.showMsg = true;
          }
        });
      var myChart = echarts.init(this.$refs.main);
      myChart.setOption({
        title: { text: "离线情况" },
        tooltip: { trigger: "axis" },
        legend: { data: ["stopcount"] },
        grid: { show: "true", borderWidth: "0" },
        xAxis: [
          {
            splitLine: "false",
            name: "时间",
            type: "time",
            data: []
          }
        ],
        yAxis: {
          name: "是否在线",
          type: "value"
        },
        series: [
          {
            name: "是否在线",
            type: "bar",
            color: "#12cf96",
            barWidth: 2,
            data: this.isOnline
          },
          {
            name: "是否在线",
            type: "bar",
            color: "#ff0000",
            barWidth: 2,
            data: this.isOffline
          }
        ]
      });
    }
  },
  mounted() {
    // this.startEndDate.qrq = this.startOfDay();
    // this.startEndDate.zrq = this.endOfDay();
    // this.getOnlineStatus();
    // this.echartData = JSON.parse(JSON.stringify(this.echartData));
    // this.getPage();
    // this.line();
  }
};
</script>
<style scoped>
  * {
    margin: 0;
    padding: 0;
    list-style: none;
  }
</style>
