<template>
  <div>
    <div>
      <div style="display: flex;justify-content: space-between">
        <div>
          <el-form label-width="80px">
            <el-row>
              <el-col :span="13">
                <el-form-item label="起始日期">
                  <el-date-picker
                    v-model="startEndDate.qrq"
                    type="date"
                    format="yyyy-MM-dd 00:00:00"
                    value-format="yyyy-MM-dd 00:00:00"
                    placeholder="选择日期"
                    :picker-options="pickerOption1"
                  >起始日期</el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item>
                  <el-button id="query" @click="flcountByComp">查询</el-button>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="13">
                <el-form-item label="结束日期">
                  <el-date-picker
                    v-model="startEndDate.zrq"
                    type="date"
                    format="yyyy-MM-dd 23:59:59"
                    value-format="yyyy-MM-dd 23:59:59"
                    placeholder="选择日期"
                  >结束日期</el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="4">
                <el-form-item>
                  <el-button id="print" @click="printfltj">打印</el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>

          <!-- <label style="font-size: 20px;font-family: 粗体">总方量：{{sum}}</label> -->
        </div>
      </div>
    </div>
    <!-- <div id="_top" style="width:1200px;height: 400px;margin-top:100px;margin-left: 300px;"></div> -->
    <div id="fltj" ref="main" style="width: 1440px;height: 400px;"></div>
  </div>
</template>

<script>
import moment from "moment";
import echarts from "echarts";
import utils from "./../utils/utils";
import { parseTime } from "@/utils/setMethods";
import { parseDate } from "@/utils/setMethods";
import { json2excel } from "@/utils/setMethods";
import { formatJson } from "@/utils/setMethods";
import printJS from "print-js";
export default {
  provide() {
    return {
      // reload: this.reload
    };
  },
  data() {
    return {
      pickerOption1: {
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "上期",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "本期",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "本年",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "上年",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          }
        ]
      },
      startEndDate: {
        qrq: moment(new Date()).format("YYYY-MM-DD 00:00:00"),
        zrq: moment(new Date()).format("YYYY-MM-DD 23:59:59")
      },
      isRouterAlive: true,
      show: true,
      beiruike: [],
      comp: [],
      fl: [],
      subheader: "统计时间区间："
    };
  },

  methods: {
    printfltj() {
      printJS({ printable: "fltj", type: "html" });
      // 按jsondata打印
      // printJS({
      //   printable: this.beiruike,
      //   type: "json",
      //   header: myheader,
      //   properties: [
      //     { field: "mc", displayName: "公司名称" },
      //     { field: "fl", displayName: "方量" }
      //   ],
      //   gridHeaderStyle: " border: 1px solid #3971A5;",
      //   gridStyle: "border: 1px solid #3971A5 ;"
      // });
    },
    flcountByComp() {
      this.comp = [];
      this.fl = [];
      this.$http.post("/Flsg/flCountByComp", this.startEndDate).then(resp => {
        if (resp) {
          this.beiruike = resp.data;
          for (var i = 0; i < this.beiruike.length; i++) {
            this.comp.push(this.beiruike[i].mc);
            this.fl.push(this.beiruike[i].fl);
            this.subheader =
              moment(this.startEndDate.qrq).format("YYYY-MM-DD") +
              "-- " +
              moment(this.startEndDate.zrq).format("YYYY-MM-DD") +
              "   总方量：" +
              this.sum;
          }
          this.setEchart();
        } else {
          this.loading = true;
          this.showMsg = true;
        }
      });
    },
    setEchart() {
      var myChart = echarts.init(this.$refs.main);
      var option = {
        title: {
          text: "混凝土方量统计(按公司)",
          subtext: "统计时间区间：" + this.subheader,
          subtextStyle: { color: "black" },
          padding: [0, 0, 500, 500]
        },
        tooltip: { trigger: "axis" },
        // legend: { data: ["this.beiruike"] },
        grid: { show: "true", borderWidth: "0" },
        xAxis: {
          splitLine: "false",
          name: "公司",
          type: "category",
          data: this.comp,
          axisLabel: {
            //---坐标轴 标签
            show: true, //---是否显示
            inside: false, //---是否朝内
            rotate: 45, //---旋转角度
            margin: 5 //---刻度标签与轴线之间的距离
            //color:'red',				//---默认取轴线的颜色
          }
        },

        yAxis: {
          name: "方量",
          type: "value",
          axisLabel: {
            //---坐标轴 标签
            show: true, //---是否显示
            inside: false, //---是否朝内
            rotate: 0, //---旋转角度
            margin: 8 //---刻度标签与轴线之间的距离
            //color:'red',				//---默认取轴线的颜色
          }
        },
        series: [
          {
            name: "方量",
            type: "bar",
            color: "#12cf96",
            barWidth: 60,
            data: this.fl,
            label: {
              show: true,
              position: "top", //---相对位置
              rotate: 0, //---旋转角度
              color: "black"
            }
          }
        ]
      };
      myChart.setOption(option);
    }
    //   生成Excel
    //   handleDownload() {
    //     var excelDatas = [
    //       {
    //         multiHeader: ["方量", ""],
    //         tHeader: ["公司名称", "方量"],
    //         filterVal: ["mc", "fl"],
    //         tableDatas: this.beiruike,
    //         sheetName: "sheet1"
    //       }
    //     ];
    //     var filename = "方量统计数据报表" + parseDate(this.startEndDate.qrq);
    //     json2excel(excelDatas, filename, true, "xlsx");
    //   }
  },

  computed: {
    sum: function() {
      let fl = 0;
      for (var i = 0; i < this.beiruike.length; i++) {
        fl = fl + this.beiruike[i].fl;
      }
      return fl.toFixed(2);
    }
  },
  mounted() {
    this.flcountByComp();
  }
};
</script>

